import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Button, Card, Table, Form, Avatar, Modal, Input, Col, Row, message} from "antd";
import {DeleteOutlined, EditOutlined, FilterOutlined, PlusOutlined, ReloadOutlined} from "@ant-design/icons";
import {useHistory} from 'react-router-dom'
import ApiService from "services/api.service";
import Utils from "../../../../../../utils";
import ImageUploadsForm from "../../../../../../components/shared-components/uploads/image-uploads.form";


function mapStateToProps(state) {
	return {};
}

function Index() {
	const history = useHistory()
	const [loading, setLoading] = useState(true)
	const [params, setParams] = useState({
		page: 1,
		limit: 20
	})
	const [pagination, setPagination] = useState({
		page: 1,
		limit: 10
	})
	const [data, setData] = useState([])
	
	const [ loadings ,setLoadings] = useState([])

	
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	
	function _caller(){
		setLoading(true)
		new ApiService({
			url: "/api/v1/master-data/airline",
			config: {
				params: {
					...params
				}
			}
		}).get()
			.then((response) => {
				setData([...response?.data])
				setPagination({
					...pagination,
					...response?.paginate
				})
				setLoading(false)
			})
			.catch((err) => {
				setLoading(false)
				setData([])
			})
	}


	useEffect(() => {
		_caller()
	}, [params])
	
	useEffect(()=> {
		form.resetFields()
	},[visible])
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	const onFinish = () => {
		form.validateFields()
			.then((value) => {
				new ApiService({
					body: value,
					url:`/api/v1/master-data/airline`,
					mode:"express",
				}).post()
					.then((response)=> {
						message.success({
							content:response?.message ?? "Successfully created",
							key:"create-city",
							duration:2
						})
						setVisible(false)
						setLoading(false)
					})
					.catch((err)=> {
						message.info(JSON.stringify(err))
						message.error({
							content:err?.message  ?? "Error: Some Error",
							key:"create-city",
							duration:2
						})
						setLoading(false)
						setVisible(false)
					})
			})
			.catch((err)=> {
			
			})
	}
	
	function handleClickEdit(_, index) {
		setSelected(null)
		form.resetFields()
		setVisible(!visible)
		setSelected(_)

	}
	
	function onCloseModal() {
		form.resetFields()
		setVisible(false)
		setSelected(null)

	}
	
	
	function handleDeleteRow(_,index){
		enterLoading(index, true)
		new ApiService({
			url: `/api/v1/master-data/airline/${_}`,
		}).delete()
			.then((response) => {
				if(response?.success){
					if(data && typeof(data[index]) !== 'undefined'){
						data[index].isDeleted = true
						setData([...data])
						enterLoading(index, false)
					}
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(()=>{
						setData([...data.filter((item)=> item.id !== _)])
					},2000)
				}else{
					enterLoading(index, false)
					message.error(response?.message ?? "Error: delete by id " + _)
				}
			})
			.catch((err) => {
				enterLoading(index, false)
			})
	}
	
	const columns= [
		{
			key: "no",
			dataIndex: "id",
			width: 70,
			render: (_, val, index) => ((index + 1) + (params?.limit * (params?.page - 1)))
		},
		{
			key: "code",
			dataIndex: ["data", "code"],
			title: "Code",
			render: (_, val, index) => _ ?? "-"
		},
		{
			key: "display-name",
			dataIndex: ["data", "name"],
			title: "Display Name",
			render: (_, val, index) => _ ?? "-"
		},
		{
			key: "description",
			dataIndex: ["data", "description"],
			title: "Description",
			ellipsis: true,
			// width:200,
			render: (_, val, index) => _ ?? "-"
		},
		{
			key: "image.dark",
			title: "Thumbnail Dark",
			dataIndex: ['data', 'image', 'dark'],
			render: (_, val, index) => {
				return (
					<Avatar
						size={'large'}
						src={[process.env.REACT_APP_API_MASTER,_].join('') ?? null}
						shape={'square'}
						alt={`${val?.data?.name}-image-dark`}
						style={{
							background: "#32bccc"
						}}
					>
						{
							!_ ?
								Utils.getNameInitial(val?.data?.name ?? "-")
								: null
						}
					</Avatar>
				)
			}
		},
		{
			key: "image.light",
			title: "Thumbnail Light",
			dataIndex: ['data', 'image', 'light'],
			render: (_, val, index) => {
				return (
					<Avatar
						size={'large'}
						src={[process.env.REACT_APP_API_MASTER,_].join('') ?? null}
						alt={`${val?.data?.name}-image-light`}
						style={{
							background: "#32bccc"
						}}
						shape={'square'}
					>
						{
							!_ ?
								Utils.getNameInitial(val?.data?.name ?? "-")
								: null
						}
					</Avatar>
				)
			}
		},
		{
			key: 'action',
			width: 120,
			dataIndex:'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2'}>
						<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}
						        onClick={() => handleClickEdit(val, index)}/>
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							onClick={()=> {
								handleDeleteRow(_,index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	]
	
	
	function onRefresh(){
		_caller()
	}
	return (
		<React.Fragment>
			
			
			<Modal
				style={{
					width: "520px !important",
				}}
				title={'Edit'}
				visible={visible} onOk={onFinish} onCancel={onCloseModal}>
				<Form form={form} initialValues={{
					...selected,
				}} layout={'vertical'}>
					<Form.Item name={'identifier'} hidden label={'Identifier'} rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'code']} label={'Code'} rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'name']} label={'Display Name'} rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'description']} label={'Description'}
					           rules={[{required: false, message: "Can't be empty!"}]}>
						<Input.TextArea
							placeholder="textarea with clear icon"
							allowClear
						/>
					</Form.Item>
					
					<Row gutter={24}>
						<Col xs={24} lg={24}>
							<Card title={'Image Light'}>
								<ImageUploadsForm
									config={{hidden: true}}
									initialValue={selected  ? selected?.data?.image?.light : null}
									type={'airline'}
									name={['data', 'image', 'light']}
									onCallback={function (src, data) {
										form.setFieldsValue({
											data: {
												image:{
													light: data?.url ?? null
												}
											}
										})
									}}
									onDelete={function (setImage) {
										form.setFieldsValue({
											data: {
												image: {
													light: null,
													
												}
											}
										})
									}}
								/>
							</Card>
						</Col>
						<Col xs={24} lg={24}>
							<Card title={'Image Dark'}>
								<ImageUploadsForm
									config={{hidden: true}}
									initialValue={selected  ? selected?.data?.image?.dark : null}
									type={'airline'}
									name={['data', 'image', 'dark']}
									onCallback={function (src, data) {
										form.setFieldsValue({
											data: {
												image:{
													dark: data?.url ?? null
												}
											}
										})
									}}
									onDelete={function (setImage) {
										form.setFieldsValue({
											data: {
												image: {
													dark: null
												}
											}
										})
									}}
								/>
							</Card>
						</Col>
					</Row>
				</Form>
			</Modal>
			
			
			
			<HeroComponents
				loading={false}
				name={"Overview"}
				positionName={['Master Data', 'Airlines']}
				thumbnail={'/overview.png'}
				extra={<>
					<ButtonPermission
						permission={['public','admin']}
						theme={{
							type:"disabled",
							property:{
								message:"Access Denied"
							}
						}}
						config={{
							onClick: function(){
								history.push('/dashboard/master-data/airlines/create')
							},
							ghost:true,
							icon:<PlusOutlined/>,
							// icon: PlusOutlined,
							type:"primary",
							size:"small"
						}}>
						Add New
					</ButtonPermission>
				</>}
			/>
			
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Card bordered={false} extra={<>
						<Button icon={<ReloadOutlined/>} ghost type={'primary'} onClick={_caller} loading={loading}/>
					</>}>
						<Table
							rowKey={'id'}
							rowClassName={(record, index) => record?.isDeleted ?"!mk-bg-red-500 !mk-bg-opacity-20":''}
							loading={loading}
							dataSource={data}
							columns={columns}
							pagination={{
								pageSize: params?.limit,
								current: params?.page,
								total: pagination?.total_items ?? 0,
								onChange: function (page, pageSize) {
									setParams({
										...params,
										page,
										limit: pageSize
									})
								}
							}}
						/>
					</Card>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);